.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader, .preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 75%;
  }
  .underline:hover::after {
    width: 40%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0;
    height: 2px;
    background-color: #2b3769;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preloader__loader {
  width: 48px;
  height: 48px;
  border: 3px solid #ea81ee;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.preloader__loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #004646 transparent;
}

.preloader-remove {
  display: none;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
